}
.hrd-box .checker .showleft span.left {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.hrd-box .checker .showright span.right {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.hrd-box .checker span.top {
